import { Box } from "@mui/material";
import LoadingIndicator from "~/components/common/LoadingIndicator";
import { LanguageSelector } from "~/components/manage/LanguageSelector";
import styles from "./AuthWrap.module.css";
import ProcessedError from "~/components/manage/ProcessedError";

export const AuthWrap = ({
  children,
  loading,
  showError,
  processedError,
  handleClose,
}) => {
  return (
    <Box className={styles.mainContainer}>
      <Box sx={{ position: "absolute", top: "25px", right: "25px" }}>
        <LanguageSelector />
      </Box>
      {loading && <LoadingIndicator />}
      {showError && (
        <ProcessedError error={processedError} handleClose={handleClose} />
      )}
      {children}
    </Box>
  );
};
